*{
  box-sizing: border-box;
}
html{
  background-color: blueviolet;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input,button{
  border: none;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
}
input{
  border: 1px solid transparent;
}
input::placeholder{
  font-size: 15px;
}
button
{
    font-weight: bold;
    color: violet;
    }
button:hover{
  background-color: rgb(182, 115, 244);
  color :white;
  cursor: pointer;
}
.completed{
  text-decoration: line-through;
  color: #888
}
.todoWrapper{
  margin-left: 40% ;
  margin-top: 50px;
}
.todoWrapper>h1{
  margin-left: 17px;
  color:white
}
.square{
  padding: 2px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
}
.text{
  color:white;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  margin-left: 10px;
  cursor: pointer;
}